.nf-help {
  background: none;
}

.nf-field-description {
  font-size: 14px;
}

.nf-form-content {
  padding: 5px;
  max-width: 1000px;
  margin: 0 auto;
}
.nf-form-content label {
  font-size: 18px;
  line-height: 20px;
}
.nf-form-content .ninja-forms-field,
.nf-form-content .listselect-wrap .nf-field-element div {
  font-size: 16px;
  margin: 0;
  padding: 12px;
}
.nf-form-content input.ninja-forms-field,
.nf-form-content select.ninja-forms-field:not([multiple]),
.nf-form-content .listselect-wrap .nf-field-element div {
  height: 50px;
}
.nf-form-content textarea.ninja-forms-field {
  height: 200px;
}
.nf-form-content hr.ninja-forms-field {
  padding: 0;
}
.nf-form-content input[type=radio].ninja-forms-field,
.nf-form-content input[type=checkbox].ninja-forms-field {
  height: auto;
}
.nf-form-content .listradio-wrap li,
.nf-form-content .listcheckbox-wrap li {
  margin-bottom: 12px !important;
  line-height: 0;
}
.nf-form-content .nf-product-field span {
  padding-right: 16px;
}
.nf-form-content .nf-product-field .nf-field-qty {
  font-size: 16px;
  padding: 5px;
  width: 80px !important;
}
.nf-form-content .nf-static {
  background: transparent;
  border: 0;
  font-weight: bold;
  padding: 0;
}
.nf-form-content .label-above .nf-field-label,
.nf-form-content .label-below .nf-field-label,
.nf-form-content .label-hidden .nf-field-label {
  margin-bottom: 10px;
  padding: 0;
}
.nf-form-content .label-below .nf-field-label {
  margin-bottom: 0;
  margin-top: 10px;
}
.nf-form-content .label-right .nf-field-label {
  margin: 0;
}
.nf-form-content .label-right > input[type=radio],
.nf-form-content .label-right input[type=checkbox] {
  margin-right: 1%;
}
.nf-form-content .label-right ul {
  margin-right: 1%;
}
